export default{
  data() {
    return {
      title: '',
      type: '',
      showInfo: false,
      code_word: '获取验证码',
      mobile: '',
      password: '',
      password2: '',
      code: '',
      btnValue: '',
      ishide: false
    }
  },
  onLoad(option) {
    this.type = option.type
    this.mobile = option.mobile
    console.log('password接收到的参数', option)
    if(option.type === 'register') {
      this.title = '设置密码'
      this.btnValue = '注册并登录'
      return
    }
    if(option.type === 'forget') {
      this.title = '重置密码'   
      this.btnValue = '保存并登录'
      return
    }
  },
  computed: {
    windowHeight() {
      return this.$store.state.windowHeight
    },
    statusBarHeight() {
      return this.$store.state.statusBarHeight
    }
  },
  methods: {
    goBack() {
      uni.navigateBack({
        delta: 1
      })
    },
    sureAction() {
      console.log('fjafjf')
      if(!this.password) {
        uni.showToast({
          title: '密码不能为空',
          icon: 'none'
        })
        return
      }
      if(this.password !== this.password2) {
        uni.showToast({
          title: '两次密码输入不一致',
          icon: 'none'
        })
        return
      }
      
      switch(this.type) {
          case 'forget':
            this.$http({
              url: this.$api.resetpassword,
              method: 'POST',
              data: {
                mobile: this.mobile,
                newpassword: this.password
              },
              cb: (err, res) => {
                if(!err && res.code === 1) {
                  this.$store.commit('login', {
                    mobile: this.mobile,
                    token: res.data.token
                  })
                  uni.showToast({
                    title: '重置密码成功',
                    icon: 'none'
                  })
                  
                  uni.switchTab({
                    url: '../index/index'
                  })
                  return
                } else if(res.code === 0 && res.msg) {
                  uni.showToast({
                    title: res.msg,
                    icon: 'none'
                  })
                  return
                } else {
                  uni.showToast({
                    title: '重置密码失败',
                    icon: 'none'
                  })
                  return
                }
              }
            })
            break
          case 'register':
            this.$http({
              url: this.$api.register,
              method: 'POST',
              data: {
                mobile: this.mobile,
                password: this.password
              },
              cb: (err, res) => {
                if(!err && res.code === 1) {
                  uni.showToast({
                    title: '注册成功',
                    icon: 'none'
                  })
                  uni.switchTab({
                    url: '../index/index'
                  })
                } else if(res.code === 0) {
                  uni.showToast({
                    title: res.msg,
                    icon: 'none'
                  })
                } else {
                  uni.showToast({
                    title: '注册失败',
                    icon: 'none'
                  })
                }
              }
            })
            break
        }
      }
      
    
  }
}
